import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { Hits, Index, useInstantSearch } from 'react-instantsearch-hooks-web';
import { Box, Text } from '@chakra-ui/react';
import theme from '../../@chakra-ui/gatsby-plugin/theme';

const NoResultsBoundary = ({ children, fallback }) => {
  const { results } = useInstantSearch();

  // The `__isArtificial` flag makes sure not to display the No Results message
  // when no hits have been returned.
  if (!results.__isArtificial && results.nbHits === 0) {
    return (
      <>
        {fallback}
        <div hidden>{children}</div>
      </>
    );
  }

  return children;
};

const NoResults = () => {
  return (
    <Box textAlign="left" px={1}>
      No results found.
    </Box>
  );
};

const PageHit = ({ hit }) => {
  const handleFocus = event => {
    event.target.scrollIntoView({ behavior: 'smooth', block: 'center' });
  };

  //rewrite absolute url to relative to make use of the performance benefits provided by Gatsby Link
  const url = new URL(hit.url);
  const path = url.pathname + url.search + url.hash;

  return (
    <Box
      _hover={{ backgroundColor: theme.colors.rlYellow40 }}
      _focusWithin={{
        backgroundColor: theme.colors.rlYellow40,
      }}
      padding="5px"
      onFocus={handleFocus}
    >
      <GatsbyLink to={path}>
        <Text noOfLines={1} fontSize="md" as="b" textAlign="left">
          {hit.title}
        </Text>
        <Text
          color={theme.colors.rlGreyDarker}
          noOfLines={1}
          as="div"
          textAlign="left"
        >
          {hit.content == null || hit.content === ''
            ? hit.description
            : hit.content}
        </Text>
      </GatsbyLink>
    </Box>
  );
};

const SearchResult = ({ indexName }) => (
  <Index indexName={indexName}>
    <NoResultsBoundary fallback={<NoResults />}>
      <Hits hitComponent={PageHit} />
    </NoResultsBoundary>
  </Index>
);

export default SearchResult;
