import {
  Box,
  Stack,
  Link as ChakraLink,
  Button,
  Text,
  Flex,
} from "@chakra-ui/react"
import React from "react"
import Dropdown from "../../dropdown/dropdown"
import { Link as GatsbyLink } from "gatsby"

const DesktopNav = ({ currentVersion, versions, currentRepo }) => {
  return (
    <Stack direction="row" spacing={8} alignItems="end">
      {/*versions dropdown*/}
      {currentVersion?.displayName != null && (
        <>
          <Flex
            alignItems="center"
            fontWeight="600"
            pr="2em"
            borderRight="1px solid white"
          >
            <Text color="white" fontSize="2xl" pr="0.5em">
              Version:
            </Text>
            <Dropdown as={Box} title={currentVersion?.displayName}>
              <Stack m={2}>
                {versions?.map(version => (
                  <GatsbyLink
                    key={version.name}
                    to={`/${currentRepo.name}/${version.name}`}
                  >
                    <Box
                      textAlign="left"
                      transition={"all .3s ease"}
                      fontWeight={600}
                      display="block"
                      p={1}
                      _hover={{ bg: "rlYellow" }}
                      cursor="pointer"
                      fontSize="md"
                    >
                      {version.displayName}
                    </Box>
                  </GatsbyLink>
                ))}
              </Stack>
            </Dropdown>
          </Flex>
        </>
      )}

      <ChakraLink
        href="https://marketplace.brainwavegrc.com/"
        style={{
          p: 1,
          fontSize: 24,
          color: "white",
          fontWeight: 600,
        }}
        _hover={{
          textDecoration: "none",
        }}
        aria-label="Go to Radiant Logic support page"
      >
        Marketplace
      </ChakraLink>

      <ChakraLink
        href="https://support.radiantlogic.com"
        style={{
          p: 1,
          fontSize: 24,
          color: "white",
          fontWeight: 600,
          textDecoration: "underline",
        }}
        aria-label="Go to Radiant Logic support page"
      >
        Support
      </ChakraLink>

      <Button
        background="rlGreen"
        color="rlBlack"
        borderRadius="0.6rem"
        fontSize="24"
        fontWeight="600"
        _hover={{
          background: "rlYellow",
        }}
        onClick={() => {
          window
            .open("https://www.radiantlogic.com/request-a-trial/", "_blank")
            .focus()
        }}
      >
        Request Trial
      </Button>
    </Stack>
  )
}
export default DesktopNav
