import React, { createRef, useState, useMemo, useLayoutEffect } from 'react';
import algoliasearch from 'algoliasearch/lite';
import {
  InstantSearch,
  PoweredBy,
  Configure,
} from 'react-instantsearch-hooks-web';
import useClickOutside from './use-click-outside';
import {
  Box,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverFooter,
  PopoverTrigger,
  Portal,
} from '@chakra-ui/react';
import SearchBox from './search-box';
import SearchResult from './search-result';
import { globalHistory } from '@reach/router';

const Search = ({ currentVersion, currentRepo }) => {
  /**
   * the name of the index from Algolia
   * @type {string}
   */
  const indexName = `developer-radiantlogic`;

  const searchBoxRef = createRef();
  const searchResultsRef = createRef();
  const [query, setQuery] = useState();
  const [hasFocus, setFocus] = useState(false);
  const searchClient = useMemo(
    () =>
      algoliasearch(
        process.env.GATSBY_ALGOLIA_APP_ID,
        process.env.GATSBY_ALGOLIA_SEARCH_KEY,
      ),
    [],
  );
  const [width, setWidth] = React.useState(0);

  useLayoutEffect(() => {
    setWidth(searchBoxRef?.current?.clientWidth);
  }, []);

  useClickOutside(searchBoxRef, searchResultsRef, () => setFocus(false));

  if (globalHistory.location.pathname === '/') return <></>;

  return (
    <InstantSearch searchClient={searchClient} indexName={indexName}>
      <Configure
        analytics={false}
        filters={`ver:${currentVersion?.name} AND prod:${currentRepo?.name}`}
        hitsPerPage={40}
      />
      <Popover
        isOpen={query && query.length > 3 && hasFocus}
        placement="bottom-start"
        initialFocusRef={searchBoxRef}
      >
        <PopoverTrigger>
          <SearchBox
            ref={searchBoxRef}
            onChange={query => setQuery(query)}
            onFocus={() => {
              setFocus(true);
              setWidth(searchBoxRef?.current?.clientWidth);
            }}
            hasFocus={hasFocus}
          />
        </PopoverTrigger>
        <Portal>
          <PopoverContent w={width}>
            <PopoverBody>
              <Box
                maxHeight="30vh"
                overflow="auto"
                tabIndex={0}
                ref={searchResultsRef}
              >
                <SearchResult indexName={indexName} />
              </Box>
            </PopoverBody>
            <PopoverFooter>
              <Box>
                <PoweredBy />
              </Box>
            </PopoverFooter>
          </PopoverContent>
        </Portal>
      </Popover>
    </InstantSearch>
  );
};

export default Search;
